<!-- 我的任务-列表 -->
<template>
  <van-row style="height:100%">
    <!-- 主体内容 -->
    <van-row>
      <van-form ref="form"
                v-model="form">
        <van-col span="16">
          <van-field v-model="form.TaskTitle"
                     class="dt-input"
                     :left-icon="require('@/assets/img/搜索.png')"
                     placeholder="请输入任务标题" />
        </van-col>
        <van-col span="4">
          <van-button native-type="submit"
                      class="dt-button"
                      @click="submit">搜索
          </van-button>
        </van-col>
        <van-col span="4">
          <div class="dt-saixuan">
            <img class="dt-saixuan-img"
                 @click="popupShow=true"
                 :src="require('@/assets/img/筛选.png')">
          </div>
        </van-col>
      </van-form>
    </van-row>
    <!-- 列表 -->
    <van-row class="dt-content">
      <van-row>
        <van-list v-model="loading"
                  :finished="finished"
                  finished-text="没有更多了"
                  :error.sync="listError"
                  error-text="请求失败，点击重新加载"
                  @load="onLoadList">
          <van-row v-for="item in orderList"
                   :key="item.ID"
                   @click="orderDetails(item)"
                   class="dt-card">
            <myCard :image="item.LinkPicture | filtImg"
                    :title="item.TaskTitle"
                    :content="item.TaskContent"></myCard>
          </van-row>
        </van-list>
      </van-row>
    </van-row>
    <!-- 弹出层 -->
    <van-popup v-model="popupShow"
               @click-overlay="filtrateCancel"
               position="right"
               class="dt-popup">
      <van-row class="dt-pop-title-main">
        <span>筛选</span>
      </van-row>
      <van-row class="dt-pop-title">
        <span>时间区域</span>
      </van-row>
      <van-row type="flex"
               justify="space-around">
        <van-col span="8"
                 class="dt-date dt-date-left"
                 @click="dateShow = true">
          <span>{{startDate | formatDate}}</span>
        </van-col>
        <van-col span="1"
                 class="dt-date-line">
          <div></div>
        </van-col>
        <van-col span="8"
                 class="dt-date dt-date-right"
                 @click="dateShow = true">
          <span>{{endDate | formatDate}}</span>
        </van-col>
      </van-row>
      <van-row class="dt-botton-box">
        <span class="dt-empty"
              @click="empty">清空</span>
        <div class="dt-bottons">
          <van-button round
                      class="dt-pop-button1"
                      type="info"
                      @click="filtrateCancel">取消</van-button>
          <van-button round
                      class="dt-pop-button2"
                      type="info"
                      @click="filtrateConfirm">确定</van-button>
        </div>
      </van-row>
    </van-popup>
    <van-calendar v-model="dateShow"
                  type="range"
                  color=" #5870f8"
                  :min-date="new Date(2020, 0, 1)"
                  @confirm="dateOnConfirm" />
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions, // 任务查询
  GetTaskuser,  // 查询用户已阅未阅列表（任务下发记录查询）
} from "@/api/testApi";
import myCard from "@/components/listCard/card";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: { myCard },
  props: ['sendForm', 'formType'],
  filters: {
    formatDate (date) {
      if (!date) {
        return '请选择日期'
      }
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      let day = date.getDate()
      if (String(month).length < 2) {
        month = '0' + month
      }
      if (String(day).length < 2) {
        day = '0' + day
      }
      return String(year) + '-' + String(month) + '-' + String(day)
    },
    filtImg (img) {
      if (img) {
        let image = img.split(',')
        return image[0]
      } else {
        return 'https://tse4-mm.cn.bing.net/th/id/OIP._SITyJkNY2j60N85kUduIAHaFj?pid=Api&rs=1'
      }
    },
  },
  data () {
    //这里存放数据
    return {
      // 表单内容
      form: {
        // // TaskCode: "", // 任务编码
        // TaskTitle: "",  // 任务名称
        // TaskStatus: 2,  // 任务状态
        // // SubmitUserId: "",  // 提交人
        // // ExamineUserId: "", // 审核人
        // //SendUserId: null, // 下发人id Int
        // PageSize: 20, // 默认20 Int
        // PageIndex: 0 // 默认1 Int
      },
      // 列表内容
      orderList: [],
      loading: false, // 列表加载状态
      finished: false, // 列表加载完成
      listError: false, // 列表加载失败
      popupShow: false, // 弹出层是否显示
      dateShow: false,  // 事件选择器是否显示
      cancelBackups: [null, null], // 筛选取消备份
      startDate: null, // 初始时间
      endDate: null, // 结束时间
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    sendForm (newDate, oldDate) {
      this.form = newDate
      this.$forceUpdate()
      this.getTestList()
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.form = this.sendForm
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 任务查询
    getTestList () {
      let queryPort = null
      if (this.formType == 1 || this.formType == 2) {
        queryPort = GetPageContentByConditions
      } else if (this.formType == 3 || this.formType == 4) {
        queryPort = GetTaskuser
      }
      queryPort(this.form).then(response => {
        if (response.Data.ContentList) {
          this.orderList = [...this.orderList, ...response.Data.ContentList]
        }
        this.totalCount = response.Data.TotalCount
        // 数据全部加载完成
        if (this.orderList.length >= this.totalCount) {
          this.finished = true;
        }
        this.loading = false
      })
    },
    // 下拉列表
    onLoadList () {
      this.form.PageIndex = this.form.PageIndex + 1
      this.getTestList()
    },
    // 查看按钮事件
    submit () {
      let queryPort = null
      if (this.formType == 1 || this.formType == 2) {
        queryPort = GetPageContentByConditions
      } else if (this.formType == 3 || this.formType == 4) {
        queryPort = GetTaskuser
      }
      queryPort(this.form).then(response => {
        if (response.Data.ContentList) {
          this.orderList = response.Data.ContentList
        } else {
          this.orderList = []
        }
        this.totalCount = response.Data.TotalCount
        // 数据全部加载完成
        if (this.orderList.length >= this.totalCount) {
          this.finished = true
        } else {
          this.finished = false
        }
        this.loading = false
      })
    },
    // 筛选确定按钮
    filtrateConfirm () {
      this.popupShow = false
      if (this.formType == 1 || this.formType == 2) {
        this.form.SubBeginDate = this.startDate
        this.form.SubEndDate = this.endDate
      } else if (this.formType == 3 || this.formType == 4) {
        this.form.SendBeginDate = this.startDate
        this.form.SendEndDate = this.endDate
      }
      this.cancelBackups = [this.startDate, this.endDate]
      this.submit()
    },
    // 筛选取消按钮
    filtrateCancel () {
      this.popupShow = false
      this.startDate = this.cancelBackups[0]
      this.endDate = this.cancelBackups[1]
      if (this.formType == 1 || this.formType == 2) {
        this.form.SubBeginDate = this.cancelBackups[0]
        this.form.SubEndDate = this.cancelBackups[1]
      } else if (this.formType == 3 || this.formType == 4) {
        this.form.SendBeginDate = this.cancelBackups[0]
        this.form.SendEndDate = this.cancelBackups[1]
      }
    },
    // 筛选清空按钮
    empty () {
      this.startDate = null
      this.endDate = null
    },
    // 时间选择器
    dateOnConfirm (date) {
      const [start, end] = date;
      this.dateShow = false;
      this.startDate = this.formatter(start) // 提交结束时间
      this.endDate = this.formatter(end) // 提交结束时间
    },
    // 将时间精确到年月日000
    formatter (d) {
      let year = d.getFullYear()
      let month = d.getMonth()
      let day = d.getDate()
      return new Date(year, month, day, 0, 0, 0)
    },
    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    // 工单详情
    orderDetails (item) {// 根据父组件传的formType判断详情应该是跳到已办详情还是待办详情
      if (this.formType === 1) {
        this.$store.commit("active", 0);
        this.$router.push({ name: 'TaskTaskReview', query: { ID: item.ID, modelType: 2, formType: 1 } })
      } else if (this.formType === 2) {
        this.$store.commit("active", 1);
        this.$router.push({ name: 'TaskTaskDetails', query: { ID: item.ID, modelType: 2, formType: 2 } })
      } else if (this.formType === 3) {
        this.$store.commit("active", 2);
        this.$router.push({ name: 'TaskTaskDetails', query: { ID: item.ID, modelType: 2, formType: 3 } })
      } else if (this.formType === 4) {
        this.$store.commit("active", 3);
        this.$router.push({ name: 'TaskTaskDetails', query: { ID: item.ID, modelType: 2, formType: 4 } })
      }
    },
  },
}
</script>
<style lang='scss' scoped>
@import '../../../styles/dimens';
//@import url(); 引入公共css类
.dt-button {
  width: px2rem(53);
  height: px2rem(30);
  margin-top: px2rem(5);
  padding: 0 0;
  background: #5870f8;
  border-radius: px2rem(5);
  font-size: px2rem(15);
  font-family: Source Han Sans CN;
  font-weight: 500;
  color: #ffffff;
}
.dt-saixuan {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: px2rem(10);
  .dt-saixuan-img {
    width: px2rem(22);
    height: px2rem(21);
  }
}
.dt-input {
  padding: px2rem(2) px2rem(6);
  margin: px2rem(5) px2rem(12);
  width: px2rem(230);
  height: $inputSearchHeight;
  background: #edf1f5;
  border-radius: px2rem(5);
}
.dt-content {
  height: calc(100% - #{$inputSearchHeight} - #{$inputSearchHeightMargin});
  overflow-y: auto;
  background: #f6f8fa;
  .dt-card {
    margin: px2rem(14) px2rem(12);
    background: #ffffff;
    border-radius: px2rem(5);
  }
}
.dt-popup {
  height: 100%;
  width: 75%;
  .dt-pop-title-main {
    margin-top: px2rem(30.6);
    text-align: left;
    font-size: px2rem(18);
    font-family: Source Han Sans CN;
    font-weight: 600;
    color: #3d3d3d;
    padding: px2rem(14) px2rem(26);
    border-bottom: solid 1px #eaeef2;
  }
  .dt-pop-title {
    text-align: left;
    font-size: px2rem(15);
    font-family: Source Han Sans CN;
    font-weight: 600;
    color: #3d3d3d;
    padding: px2rem(14) px2rem(26);
  }
  .dt-date {
    height: px2rem(30);
    text-align: center;
    line-height: px2rem(30);
    font-size: px2rem(14);
    font-family: Source Han Sans CN;
    font-weight: 400;
    background: #edf1f5;
    border-radius: px2rem(5);
    opacity: 0.5;
  }
  .dt-date-left {
    margin-left: px2rem(22);
  }
  .dt-date-right {
    margin-right: px2rem(22);
  }
  .dt-date-line {
    display: flex;
    justify-content: center;
    align-items: center;
    height: px2rem(30);
    div {
      width: px2rem(9);
      height: px2rem(2);
      background: #edf1f5;
    }
  }
  .dt-botton-box {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: px2rem(80);
    border-top: solid px2rem(1) #eaeef2;
    .dt-empty {
      float: left;
      margin-left: px2rem(10);
      color: #5870f8;
      font-size: px2rem(13);
      line-height: px2rem(80);
    }
    .dt-bottons {
      position: absolute;
      bottom: 0;
      right: 0;
      height: px2rem(80);
      display: flex;
      align-items: center;
      .dt-pop-button1 {
        width: px2rem(80);
        height: px2rem(30);
        padding: 0;
        margin-right: px2rem(18);
        background: #ffffff;
        border: px2rem(1) solid #5870f8;
        border-radius: px2rem(15);
        font-size: px2rem(13);
        font-family: Source Han Sans CN;
        font-weight: 400;
        line-height: px2rem(30);
        color: #5870f8;
      }
      .dt-pop-button2 {
        width: px2rem(80);
        height: px2rem(30);
        padding: 0;
        margin-right: px2rem(23);
        background: #5870f8;
        border-radius: px2rem(15);
        font-size: px2rem(13);
        font-family: Source Han Sans CN;
        font-weight: 400;
        line-height: px2rem(30);
        color: #ffffff;
      }
    }
  }
}
/deep/.van-field__body {
  height: 100%;
}
</style>